@import url('https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap');

:root {
    --font-family: 'Manrope', sans-serif;

    --gradient-text: linear-gradient(89.97deg, #AE67FA 1.84%, #F49867 102.67%);
    --gradient-bar: linear-gradient(103.22deg, #AE67FA -13.86%, #F49867 99.55%);

    --color-bg: #040C18;
    --color-footer: #150334;
    --color-blog: #042c54;
    --color-text: #8f81dd;;
    --color-subtext: #e9a267;
}

* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient {


    /* ff 3.6+ */
    background: -moz-radial-gradient(circle at 4% 26%, rgba(68, 26, 147, 1) 0%, rgba(2, 1, 2, 1) 100%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(circle at 4% 26%, rgba(68, 26, 147, 1) 0%, rgba(2, 1, 2, 1) 100%);

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 4% 26%, rgba(68, 26, 147, 1) 0%, rgba(2, 1, 2, 1) 100%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 4% 26%, rgba(68, 26, 147, 1) 0%, rgba(2, 1, 2, 1) 100%);

    /* global 92%+ browsers support */
    background: radial-gradient(circle at 4% 26%, rgba(68, 26, 147, 1) 0%, rgba(2, 1, 2, 1) 100%);

}

.gradient_text {
    background: var(--gradient-text);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section_padding {
    padding: 2rem 6rem;
}

.section_margin {
    margin: 4rem 6rem;
}

.scale-up-center {
    -webkit-animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: scale-up-center 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }

    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}


@media screen and (max-width: 780px) {
    .section_padding {
        padding: 2rem;
    }

    .section_margin {
        padding: 2rem;
        margin: 4rem 2.5rem;
    }
}

@media screen and (max-width: 550px) {
    .section_padding {
        padding: 4rem 2rem;
    }

    .section_margin {
        margin: 4rem 2rem;
        padding: 4rem 1.5rem;
    }

}
